}

/* ---------- Dismiss a few elements if small screen: ----------------- */
@media (max-width: 1200px) {
  .hide-if-small-screen {
    display: none;
  }
}

/* ---------- Disabled Links ----------------- */
.isDisabled {
  color: currentColor;
  cursor: not-allowed;
  opacity: 0.5;
  text-decoration: none;
}


/* ---------- Button ----------------- */
.btn {
  font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 500;
}

.btn-xl {
  font-size: 18px;
  padding: 20px 40px;
  margin: 3px;
}

.btn-primary {
  background-color: transparent;
  border-color: #69b3a2;
  color: #69b3a2;
  border-width: medium;
}

.btn-primary:active,
.btn-primary:focus,
.btn-primary:hover {
  background-color: #69b3a2 !important;
  border-color: #69b3a2 !important;
  color: white;
}

.btn-primary:active,
.btn-primary:focus {
  box-shadow: 0 0 0 0.2rem rgba(254, 209, 55, 0.5) !important;
}


.btn-secondary {
  background-color: transparent;
  border-color: #69b3a2;
  color: #69b3a2;
  border-width: small;
}

.btn-secondary:active,
.btn-secondary:focus,
.btn-secondary:hover {
  background-color: #69b3a2 !important;
  border-color: #69b3a2 !important;
  color: white;
}

.btn-secondary:active,
.btn-secondary:focus {
  background-color: #69b3a2 !important;
}

.btn-secondary:not(:disabled):not(.disabled).active {
  background-color: #69b3a2 !important;
  border-color: #69b3a2 !important;
  color: white;
}



::-moz-selection {
  background: #69b3a2;
  text-shadow: none;
}

::selection {
  background: #69b3a2;
  text-shadow: none;
}

img::selection {
  background: transparent;
}

img::-moz-selection {
  background: transparent;
}

